.contact-information {
    display: flex;
    flex-direction: row;
}

.contact {
    background-color: #ffffff;
    margin: auto;
    text-align: center;
    padding: 0 5em 0 5em;
}

.contact h1 {
    text-decoration: underline;
}

.contact ul {
    list-style: none;
}

.notification .message {
    margin: auto;
    background-color: green;
    max-width: 60%;
    border-radius: 5px;
}
.notification .error {
    margin: auto;
    background-color: red;
    max-width: 60%;
    border-radius: 5px;
}
